import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import {BrowserRouter, Link, Route, Switch} from "react-router-dom";
import About from "./pages/about/About";
import {Button, Form, FormControl, Nav, Navbar, NavDropdown} from "react-bootstrap";
import Footer from "./pages/Footer";
import 'bootstrap/dist/css/bootstrap.min.css';
import Project from "./pages/projects/Project";
import Web from "./pages/projects/web/Web";
import Mobile from "./pages/projects/mobile/Mobile";
import Desktop from "./pages/projects/desktop/Desktop";
import Software from "./pages/projects/software/Software";

const projects = require('./pages/projects/projects.json');
const createSubMenu = ( (projects:any) => {
    let submenu:any = [];

    projects["project-types"].map( (each_type:any) => {
        let type_heading = each_type["type-name"];
        let url = each_type["url"];

        submenu.push(
            <NavDropdown.Item><Link to={url}>{type_heading}</Link></NavDropdown.Item>
        );

    });

    return submenu;
});

ReactDOM.render(
  <BrowserRouter>
      <div>
          <Navbar className={"navigation-bar"}>
              <Navbar.Brand><Link to={"/"}>Dhruv Padalia</Link></Navbar.Brand>
              <Nav className="mr-auto">
                  <Nav.Link className={"nav-title"}><Link to={"/"}>Home</Link></Nav.Link>
                  <Nav.Link><Link to={"/about"}>About</Link></Nav.Link>
                  <NavDropdown title="Projects" id="basic-nav-dropdown">
                      {createSubMenu(projects)}
                  </NavDropdown>
              </Nav>
          </Navbar>

          <Switch>
              <Route exact path="/">
                  <App />
              </Route>
              <Route path="/about">
                  <About />
              </Route>
              <Route path="/project">
                  <Project />
              </Route>
              <Route path="/web-projects">
                  <Web />
              </Route>
              <Route path="/mobile-projects">
                  <Mobile />
              </Route>
              <Route path="/desktop-projects">
                  <Desktop />
              </Route>
              <Route path="/software-projects">
                  <Software />
              </Route>
          </Switch>

          <Footer/>
      </div>
  </BrowserRouter>,
  document.getElementById('root')
);

reportWebVitals();
